import React, { Component } from 'react';
import './App.css';


export default class TabItem extends Component {

  // Properties used by this component:
  // title, visualStateIndex

  // --- Functions for component state index 0 (1 of 2) --- 
  
  renderState0() {
    // eslint-disable-next-line no-unused-vars
    let baseStyle = {};
    // eslint-disable-next-line no-unused-vars
    let layoutFlowStyle = {};
    
    const style_textBlock3 = {
        color: 'rgba(197, 253, 241, 0.7636)',
        textAlign: 'center',
     };
    const value_textBlock3 = this.props.title;
    
    
    return (
      <div className="TabItem" style={baseStyle}>
        <div className="foreground">
          <div className='actionFont state0_elTextBlock3' style={style_textBlock3}>
            <div>{value_textBlock3 !== undefined ? value_textBlock3 : (<span className="propValueMissing">{this.props.locStrings.tabitem_textblock3_967327}</span>)}</div>
          </div>
        </div>
      </div>
    )
  }
  
  // --- Functions for component state index 1 (2 of 2) --- 
  
  renderState1() {
    // eslint-disable-next-line no-unused-vars
    let baseStyle = {};
    // eslint-disable-next-line no-unused-vars
    let layoutFlowStyle = {};
    
    const style_textBlock3 = {
        color: '#9efce6',
        textAlign: 'center',
     };
    const value_textBlock3 = this.props.title;
    
    const style_rectangle = {
        background: 'rgba(150, 252, 226, 1.000)',
     };
    
    return (
      <div className="TabItem" style={baseStyle}>
        <div className="foreground">
          <div className='actionFont state1_elTextBlock3' style={style_textBlock3}>
            <div>{value_textBlock3 !== undefined ? value_textBlock3 : (<span className="propValueMissing">{this.props.locStrings.tabitem_textblock3_426939}</span>)}</div>
          </div>
          <div className='state1_elRectangle' style={style_rectangle} />
        </div>
      </div>
    )
  }
  
  
  render() {
    switch (parseInt((this.state && this.state.visualStateIndexOverride !== undefined) ? this.state.visualStateIndexOverride : this.props.visualStateIndex, 10)) {
      default:
      case 0:
        return this.renderState0();
      case 1:
        return this.renderState1();
    }
  }
  

}



