import React, { Component } from 'react';
import './App.css';


export default class SellRates extends Component {

  // Properties used by this component:
  // rates

  render() {
    // eslint-disable-next-line no-unused-vars
    let baseStyle = {};
    // eslint-disable-next-line no-unused-vars
    let layoutFlowStyle = {};
    
    const style_dollarSign = {
        fontSize: 40.9,
        color: 'rgba(157, 243, 255, 0.3057)',
        textAlign: 'left',
     };
    const style_dollarHeader = {
        color: '#feffff',
        textAlign: 'center',
     };
    let transformPropValue_dollarValue = (input) => {
      var input = parseFloat(input);
      if (isNaN(input))
        return "...";
      
      return ((1/input)*100).toFixed(2);
    }
    const style_dollarValue = {
        fontSize: 23.7,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        fontWeight: 'bold',
        color: '#feffff',
        textAlign: 'right',
     };
    const value_dollarValue = transformPropValue_dollarValue(this.props.rates ? this.props.rates.USD : '');
    
    const style_dollarInfoText = {
        fontSize: 15.1,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        color: 'rgba(254, 255, 255, 0.6676)',
        textAlign: 'left',
     };
    const style_poundSign = {
        fontSize: 40.9,
        color: 'rgba(157, 243, 255, 0.3057)',
        textAlign: 'left',
     };
    const style_poundHeader = {
        color: '#feffff',
        textAlign: 'center',
     };
    let transformPropValue_poundValue = (input) => {
      var input = parseFloat(input);
      if (isNaN(input))
        return "...";
      
      return ((1/input)*100).toFixed(2);
    }
    const style_poundValue = {
        fontSize: 23.7,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        fontWeight: 'bold',
        color: '#feffff',
        textAlign: 'right',
     };
    const value_poundValue = transformPropValue_poundValue(this.props.rates ? this.props.rates.GBP : '');
    
    const style_poundInfoText = {
        fontSize: 15.1,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        color: 'rgba(254, 255, 255, 0.6676)',
        textAlign: 'left',
     };
    const style_CADSign = {
        fontSize: 40.9,
        color: 'rgba(157, 243, 255, 0.3057)',
        textAlign: 'left',
     };
    const style_CADHeader = {
        color: '#feffff',
        textAlign: 'center',
     };
    let transformPropValue_CADValue = (input) => {
      var input = parseFloat(input);
      if (isNaN(input))
        return "...";
      
      return ((1/input)*100).toFixed(2);
    }
    const style_CADValue = {
        fontSize: 23.7,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        fontWeight: 'bold',
        color: '#feffff',
        textAlign: 'right',
     };
    const value_CADValue = transformPropValue_CADValue(this.props.rates ? this.props.rates.CAD : '');
    
    const style_CADInfoText = {
        fontSize: 15.1,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        color: 'rgba(254, 255, 255, 0.6676)',
        textAlign: 'left',
     };
    
    return (
      <div className="SellRates" style={baseStyle}>
        <div className="layoutFlow">
          <div className="flowRow flowRow_SellRates_elDollarSign_206006">
          <div className='font-avenirNextRegular  elDollarSign'>
            <div style={style_dollarSign}>
              <div>{this.props.locStrings.sellrates_eurosign_206006}</div>
            </div>
          
          </div>
          
          </div>
          <div className='baseFont elDollarHeader'>
            <div style={style_dollarHeader}>
              <div><div dangerouslySetInnerHTML={{__html: this.props.locStrings.sellrates_euroheader_537923.replace(/\n/g, '<br>')}}></div></div>
            </div>
          
          </div>
          
          <div className="flowRow flowRow_SellRates_elDollarValue_118683">
          <div className='elDollarValue'>
            <div style={style_dollarValue}>
              <div>{value_dollarValue !== undefined ? value_dollarValue : (<span className="propValueMissing">{this.props.locStrings.sellrates_eurovalue_118683}</span>)}</div>
            </div>
          
          </div>
          
          </div>
          <div className='elDollarInfoText'>
            <div style={style_dollarInfoText}>
              <div>{this.props.locStrings.sellrates_euroinfotext_757728}</div>
            </div>
          
          </div>
          
          <div className="flowRow flowRow_SellRates_elPoundSign_433216">
          <div className='font-avenirNextRegular  elPoundSign'>
            <div style={style_poundSign}>
              <div>{this.props.locStrings.sellrates_poundsign_433216}</div>
            </div>
          
          </div>
          
          </div>
          <div className='baseFont elPoundHeader'>
            <div style={style_poundHeader}>
              <div><div dangerouslySetInnerHTML={{__html: this.props.locStrings.sellrates_poundheader_19606.replace(/\n/g, '<br>')}}></div></div>
            </div>
          
          </div>
          
          <div className="flowRow flowRow_SellRates_elPoundValue_609236">
          <div className='elPoundValue'>
            <div style={style_poundValue}>
              <div>{value_poundValue !== undefined ? value_poundValue : (<span className="propValueMissing">{this.props.locStrings.sellrates_poundvalue_609236}</span>)}</div>
            </div>
          
          </div>
          
          </div>
          <div className='elPoundInfoText'>
            <div style={style_poundInfoText}>
              <div>{this.props.locStrings.sellrates_poundinfotext_700823}</div>
            </div>
          
          </div>
          
          <div className="flowRow flowRow_SellRates_elCADSign_577537">
          <div className='font-avenirNextRegular  elCADSign'>
            <div style={style_CADSign}>
              <div>{this.props.locStrings.sellrates_cadsign_577537}</div>
            </div>
          
          </div>
          
          </div>
          <div className='baseFont elCADHeader'>
            <div style={style_CADHeader}>
              <div>{this.props.locStrings.sellrates_cadheader_432596}</div>
            </div>
          
          </div>
          
          <div className="flowRow flowRow_SellRates_elCADValue_863593">
          <div className='elCADValue'>
            <div style={style_CADValue}>
              <div>{value_CADValue !== undefined ? value_CADValue : (<span className="propValueMissing">{this.props.locStrings.sellrates_cadvalue_863593}</span>)}</div>
            </div>
          
          </div>
          
          </div>
          <div className='elCADInfoText'>
            <div style={style_CADInfoText}>
              <div>{this.props.locStrings.sellrates_cadinfotext_911027}</div>
            </div>
          
          </div>
          
        </div>
      </div>
    )
  }
  

}



