import React, { Component } from 'react';
import './App.css';
import SellRates from './SellRates';
import BuyRates from './BuyRates';
import TabItem from './TabItem';


export default class StartScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo, date, rates

  constructor(props) {
    super(props);
    
    this.state = {
      elRatesContainer_stateIndex: 0,
      selectedIndex_selector: 0,
    };
  }

  selectorSelectionChanged = (idx, ev) => {
    this.setState({selectedIndex_selector: idx});
  }
  
  render() {
    // eslint-disable-next-line no-unused-vars
    let baseStyle = {};
    // eslint-disable-next-line no-unused-vars
    let layoutFlowStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const style_backgroundShape = {
        background: 'linear-gradient(167deg, #2d82ae 0.0%, #0f2650 100.0%)',
     };
    let contentElement_elRatesContainer;
    switch (this.state.selectedIndex_selector) {
      default:
      case 0:
        contentElement_elRatesContainer = (<BuyRates rates={this.props.rates || "{\"CAD\":1.5095,\"PLN\":4.2974,\"THB\":37.026,\"BGN\":1.9558,\"SEK\":10.261,\"TRY\":6.0799,\"ILS\":4.2604,\"BRL\":4.4129,\"RUB\":75.2265,\"GBP\":0.89835,\"DKK\":7.4656,\"MXN\":23.0211,\"CHF\":1.1254,\"AUD\":1.5753,\"HRK\":7.3905,\"RON\":4.6558,\"ISK\":140.4,\"SGD\":1.5534,\"NOK\":9.7235,\"HUF\":323.93,\"NZD\":1.6613,\"USD\":1.1285,\"MYR\":4.7243,\"IDR\":16499.62,\"KRW\":1280.17,\"JPY\":128.13,\"PHP\":59.98,\"INR\":81.22499999999999,\"CZK\":25.794,\"HKD\":8.816800000000001,\"ZAR\":16.2186,\"CNY\":7.788}"} appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} />);
      break;
      case 1:
        contentElement_elRatesContainer = (<SellRates rates={this.props.rates || "{\"CAD\":1.5095,\"PLN\":4.2974,\"THB\":37.026,\"BGN\":1.9558,\"SEK\":10.261,\"TRY\":6.0799,\"ILS\":4.2604,\"BRL\":4.4129,\"RUB\":75.2265,\"GBP\":0.89835,\"DKK\":7.4656,\"MXN\":23.0211,\"CHF\":1.1254,\"AUD\":1.5753,\"HRK\":7.3905,\"RON\":4.6558,\"ISK\":140.4,\"SGD\":1.5534,\"NOK\":9.7235,\"HUF\":323.93,\"NZD\":1.6613,\"USD\":1.1285,\"MYR\":4.7243,\"IDR\":16499.62,\"KRW\":1280.17,\"JPY\":128.13,\"PHP\":59.98,\"INR\":81.22499999999999,\"CZK\":25.794,\"HKD\":8.816800000000001,\"ZAR\":16.2186,\"CNY\":7.788}"} appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} />);
      break;
    }
    
    const style_ratesContainer_outer = {
        pointerEvents: 'auto',
     };
    const style_rectangle = {
        background: 'rgba(31, 110, 159, 0.907)',
        boxShadow: '0.0px 2.9px 37px rgba(0, 0, 0, 0.2000)',
     };
    const style_line2 = {
        borderTop: '1px solid rgba(245, 246, 246, 0.5275)',
     };
    const style_selector = {
        overflow: 'hidden',  // This element is not in scroll flow
        pointerEvents: 'auto',
     };
    // eslint-disable-next-line no-unused-vars
    const style_selector_part = {
        display: 'inline-block',
        position: 'relative',
        width: '79px',
     };
    let transformPropValue_date = (input) => {
      return "Last updated "+input;
    }
    const style_date = {
        fontSize: 12.9,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        color: 'rgba(158, 252, 230, 0.3553)',
        textAlign: 'right',
     };
    // eslint-disable-next-line no-unused-vars
    const style_date_inner = {
        textAlign: 'right',
     };
    const value_date = transformPropValue_date(this.props.date);
    
    const style_dataSourceInfo = {
        fontSize: 12.9,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        color: 'rgba(158, 252, 230, 0.3553)',
        textAlign: 'right',
     };
    // eslint-disable-next-line no-unused-vars
    const style_dataSourceInfo_inner = {
        textAlign: 'right',
     };
    
    return (
      <div className="AppScreen StartScreen" style={baseStyle}>
        <div className="background">
          <div className='containerMinHeight elBackgroundShape' style={style_backgroundShape} />
        </div>
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className='hasNestedComps elRatesContainer' style={style_ratesContainer_outer}>
            <div>
              {contentElement_elRatesContainer}
            </div>
          
          </div>
          
        </div>
        <div className="screenFgContainer">
          <div className="foreground">
            <div className='elRectangle' style={style_rectangle} />
            <div className='elLine2' style={style_line2} />
            <div className='hasNestedComps elSelector' style={style_selector}>
              <div style={style_selector_part}  onClick={this.selectorSelectionChanged.bind(this, 0)}><TabItem visualStateIndex={this.state.selectedIndex_selector === 0 ? 1 : 0} title='BUY' appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} /></div>
              <div style={style_selector_part}  onClick={this.selectorSelectionChanged.bind(this, 1)}><TabItem visualStateIndex={this.state.selectedIndex_selector === 1 ? 1 : 0} title='SELL' appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} /></div>
            </div>
            <div className='elDate' style={style_date}>
              <div className="bottomAlignedContent">{value_date !== undefined ? value_date : (<span className="propValueMissing">{this.props.locStrings.start_date_683489}</span>)}</div>
            </div>
            <div className='elDataSourceInfo' style={style_dataSourceInfo}>
              <div className="bottomAlignedContent">{this.props.locStrings.start_datasourceinfo_710462}</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
  

}



