import React, { Component } from 'react';
import './App.css';


export default class BuyRates extends Component {

  // Properties used by this component:
  // rates

  render() {
    // eslint-disable-next-line no-unused-vars
    let baseStyle = {};
    // eslint-disable-next-line no-unused-vars
    let layoutFlowStyle = {};
    
    const style_textBlock2 = {
        color: '#feffff',
        textAlign: 'center',
     };
    const style_dollarSign = {
        fontSize: 40.9,
        color: 'rgba(157, 243, 255, 0.3057)',
        textAlign: 'left',
     };
    let transformPropValue_dollarValue = (input) => {
      var input = parseFloat(input);
      if (isNaN(input))
        return "...";
      
      return (input*100).toFixed(2);
    }
    const style_dollarValue = {
        fontSize: 23.7,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        fontWeight: 'bold',
        color: '#feffff',
        textAlign: 'right',
     };
    const value_dollarValue = transformPropValue_dollarValue(this.props.rates ? this.props.rates.USD : '');
    
    const style_dollarInfoText = {
        fontSize: 15.1,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        color: 'rgba(254, 255, 255, 0.6676)',
        textAlign: 'left',
     };
    const style_poundSign = {
        fontSize: 40.9,
        color: 'rgba(157, 243, 255, 0.3057)',
        textAlign: 'left',
     };
    let transformPropValue_poundValue = (input) => {
      var input = parseFloat(input);
      if (isNaN(input))
        return "...";
      
      return (input*100).toFixed(2);
    }
    const style_poundValue = {
        fontSize: 23.7,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        fontWeight: 'bold',
        color: '#feffff',
        textAlign: 'right',
     };
    const value_poundValue = transformPropValue_poundValue(this.props.rates ? this.props.rates.GBP : '');
    
    const style_poundInfoText = {
        fontSize: 15.1,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        color: 'rgba(254, 255, 255, 0.6676)',
        textAlign: 'left',
     };
    const style_CADSign = {
        fontSize: 40.9,
        color: 'rgba(157, 243, 255, 0.3057)',
        textAlign: 'left',
     };
    let transformPropValue_CADValue = (input) => {
      var input = parseFloat(input);
      if (isNaN(input))
        return "...";
      
      return (input*100).toFixed(2);
    }
    const style_CADValue = {
        fontSize: 23.7,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        fontWeight: 'bold',
        color: '#feffff',
        textAlign: 'right',
     };
    const value_CADValue = transformPropValue_CADValue(this.props.rates ? this.props.rates.CAD : '');
    
    const style_CADInfoText = {
        fontSize: 15.1,
        fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", sans-serif',
        color: 'rgba(254, 255, 255, 0.6676)',
        textAlign: 'left',
     };
    
    return (
      <div className="BuyRates" style={baseStyle}>
        <div className="layoutFlow">
          <div className='baseFont elTextBlock2'>
            <div style={style_textBlock2}>
              <div>{this.props.locStrings.buyrates_textblock2_11924}</div>
            </div>
          
          </div>
          
          <div className="flowRow flowRow_BuyRates_elDollarSign_809105">
          <div className='font-avenirNextRegular  elDollarSign'>
            <div style={style_dollarSign}>
              <div>{this.props.locStrings.buyrates_dollarsign_809105}</div>
            </div>
          
          </div>
          
          <div className='elDollarValue'>
            <div style={style_dollarValue}>
              <div>{value_dollarValue !== undefined ? value_dollarValue : (<span className="propValueMissing">{this.props.locStrings.buyrates_dollarvalue_1022924}</span>)}</div>
            </div>
          
          </div>
          
          </div>
          <div className='elDollarInfoText'>
            <div style={style_dollarInfoText}>
              <div>{this.props.locStrings.buyrates_dollarinfotext_1014830}</div>
            </div>
          
          </div>
          
          <div className="flowRow flowRow_BuyRates_elPoundSign_452966">
          <div className='font-avenirNextRegular  elPoundSign'>
            <div style={style_poundSign}>
              <div>{this.props.locStrings.buyrates_poundsign_452966}</div>
            </div>
          
          </div>
          
          <div className='elPoundValue'>
            <div style={style_poundValue}>
              <div>{value_poundValue !== undefined ? value_poundValue : (<span className="propValueMissing">{this.props.locStrings.buyrates_poundvalue_556875}</span>)}</div>
            </div>
          
          </div>
          
          </div>
          <div className='elPoundInfoText'>
            <div style={style_poundInfoText}>
              <div>{this.props.locStrings.buyrates_poundinfotext_979944}</div>
            </div>
          
          </div>
          
          <div className="flowRow flowRow_BuyRates_elCADSign_64678">
          <div className='font-avenirNextRegular  elCADSign'>
            <div style={style_CADSign}>
              <div>{this.props.locStrings.buyrates_cadsign_64678}</div>
            </div>
          
          </div>
          
          <div className='elCADValue'>
            <div style={style_CADValue}>
              <div>{value_CADValue !== undefined ? value_CADValue : (<span className="propValueMissing">{this.props.locStrings.buyrates_cadvalue_491148}</span>)}</div>
            </div>
          
          </div>
          
          </div>
          <div className='elCADInfoText'>
            <div style={style_CADInfoText}>
              <div>{this.props.locStrings.buyrates_cadinfotext_207699}</div>
            </div>
          
          </div>
          
        </div>
      </div>
    )
  }
  

}



